@import url("https://fonts.cdnfonts.com/css/montserrat");
@import url("https://fonts.cdnfonts.com/css/montserrat");
.menu {
  position: relative;
}

.menu__btn {
  width: 30px;
  height: 30px;
  position: relative;
  display: block;
  line-height: 0;
  background-image: url("../images/burger-menu.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.menu__list {
  position: absolute;
  right: 0;
  top: 20px;
  list-style: none;
  background-color: #ffffff;
  color: #666666;
  border-top: 2px solid #2ea3f2;
}

.menu__item {
  padding: 10px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.03);
}

.menu__link {
  color: inherit;
  text-decoration: none;
}

.hide {
  display: none;
}

.header {
  padding: 20px 0;
}

.header__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  margin: 0 auto;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
